import { createMarkupMovies } from "../layoutMovies";
import { removeDuplicates } from "../removeDuplicates";
import { moviesList } from "./addMovieAPI.js";

export const changeMovieAPI = () => {
  const movieToChange = {
    title: "The Knight and Day",
    director: "James Mangold",
  };

  const options = {
    method: "PATCH",
    body: JSON.stringify(movieToChange),
    headers: {
      "Content-Type": "application/json; charset=UTF-8",
    },
  };

  fetch(`http://localhost:3000/movies/5`, options)
    .then((data) => data.json())
    .then((data) => {
      console.log(data);
      removeDuplicates();
      createMarkupMovies(moviesList);
    });
};
