import { moviesList } from "./addMovieAPI.js";
import { removeDuplicates } from "../removeDuplicates.js";

export const getMoviesAPI = () => {
  return fetch("http://localhost:3000/movies")
    .then((data) => data.json())
    .then((data) => {
      moviesList.length = 0;
      moviesList.push(...data);
      removeDuplicates();
    });
};